@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700&display=swap);
*,
*::after,
*::before {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

html,
body {
	margin: 0;
	padding: 0;
}

body {
	font-family: Raleway;
	background-color: #2b4162;
}

input,
select {
	outline: none;
}

.sorted {
	-webkit-animation: sorted 0.8s forwards;
	        animation: sorted 0.8s forwards;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

@-webkit-keyframes sorted {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0.99);
	}
	75% {
		transform: scale(1.01);
	}
	100% {
		transform: scale(1);
	}
}

@keyframes sorted {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(0.99);
	}
	75% {
		transform: scale(1.01);
	}
	100% {
		transform: scale(1);
	}
}

.card {
	background-color: #e0e0e2;
	border-radius: 0.5rem;
	width: 80%;
	margin: 1rem auto;
	padding: 0.5rem;
	box-shadow: 0.7rem 0.7rem 0.5rem rgba(0, 0, 0, 0.1);
}

.card:first-of-type {
	margin-top: 0;
}

.controls {
	text-align: center;
}

.controls input {
	margin-top: 1rem;
}

.controls .input-group {
	display: flex;
	flex-direction: column;
	position: relative;
}

.controls .input-group:not(:last-of-type) {
	margin-bottom: 1rem;
}

.controls .range-labels {
	display: flex;
	justify-content: space-between;
	font-size: 0.7rem;
}

.controls .range-labels p {
	margin: 0.5rem 0 0;
}

.controls .error-message {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	transition: all 0.3s;
	color: #721817;
	font-size: 0.7rem;
	width: 100%;
}

.controls .show-error {
	top: 70%;
	opacity: 1;
}

.controls input[type="number"] {
	text-align-last: center;
	font-family: inherit;
	border-radius: 0.5rem;
	border: none;
	color: #e0e0e2;
	background-color: #2b4162;
	width: 20%;
	margin-left: auto;
	padding: 0.25rem 0.5rem;
}

/* range styles stolen from https://codepen.io/joshbader/pen/MZMzjr */
.controls input[type="range"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	     appearance: none;
	position: relative;
	width: 100%;
	height: 2em;
	padding: 0.375em;
	background-color: #2b4162;
	border-radius: 1em;
	outline: none;
	cursor: pointer;
}

.controls input[type="range"]::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	/* position: relative; */
	z-index: 10;
	width: 1.5em;
	height: 1.5em;
	background-color: #fa9f42;
	border-radius: 50%;
	cursor: pointer;
}

.controls input[type="range"]::-moz-range-thumb {
	/* position: relative; */
	z-index: 10;
	width: 1.5em;
	height: 1.5em;
	background-color: #fa9f42;
	border: none;
	border-radius: 50%;
	cursor: pointer;
}

.controls input[type="range"]::-moz-range-track {
	background-color: transparent;
}

input[type="range"]::-moz-focus-outer {
	border: 0;
}

.bars {
	margin: 2rem 0;
	height: 300px;
	display: flex;
	justify-content: center;
	align-items: flex-end;
}

select {
	width: 100%;
	font-family: inherit;
	text-align-last: center;
	border-radius: 2rem;
	border: none;
	padding: 1rem;
	background-color: #fa9f42;
	color: #fff;
}

option {
	text-align-last: center;
}

.bar {
	display: inline-block;
	width: 0.5rem;
	/* border: 1px solid black; */
	border-radius: 0.5rem;
	margin: 0.5rem 0.05rem;
	background-color: #577590;
	box-shadow: 0.3rem 0.5rem 0.5rem rgba(0, 0, 0, 0.2);
}

.button {
	padding: 0.5rem 1rem;
	color: #e0e0e2;
	background-color: #0b6e4f;
	border: none;
	font-family: inherit;
	font-size: 0.8rem;
	border-radius: 0.5rem;
	cursor: pointer;
	outline: none;
	margin: 0.5rem;
	transition: all 0.2s;
}

.button:hover {
	transform: translateY(-0.1rem);
	box-shadow: 0.1rem 0.1rem 0.5rem rgba(0, 0, 0, 0.2);
}

.button:active {
	transform: translateY(0);
	box-shadow: none;
}

.button:disabled {
	opacity: 50%;
	cursor: not-allowed;
}

.button.burgundy {
	background-color: #721817;
}




